
import { defineComponent } from 'vue'
import MultiColumnView from "@/components/containers/MultiColumnView.vue"
import FieldMixinVue from './FieldMixin.vue'
import { Option } from '../Forms/FieldInterface'
import { isEmpty } from 'lodash'
import ViewPort from "@/components/DataViews/ViewPort.vue"
import {
    IonCheckbox,
    IonItem,
    IonLabel,
} from "@ionic/vue"

export default defineComponent({
    name: "HisGridSelector",
    components: {
        ViewPort,
        IonItem,
        IonLabel,
        IonCheckbox,
        MultiColumnView
    },
    mixins: [FieldMixinVue],
    data: () => ({
        listData: [] as Option[]
    }),
    methods: {
        clearValue(){
            this.listData = this.listData.map((item) => {
                item.isChecked = false
                return item
            })
        },
        async init () {
            this.$emit('onFieldActivated', this)
            this.listData = await this.options(this.fdata, this.cdata, this.listData)
        }
    },
    watch: {
        listData: {
            handler(items: Option[]) {
                if (!isEmpty(items)) {
                    this.$emit('onValue', this.listData.filter(i => i.isChecked))
                }
            },
            immediate: true,
            deep: true
        }
    },
    mounted() {
        this.init()
    },
    activated() {
        this.init()
    }
})
